<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <section class="section">
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="consumerKey">
            <abbr class="slds-required" title="required">* </abbr>Consumer Key
          </label>
          <div class="slds-form-element__control">
            <input id="consumerKey" class="slds-input" type="text" v-model="appSetting.ConsumerKey"/>
          </div>
        </div>
        <div class="slds-form-element form-section">
          <label class="slds-form-element__label" for="consumerSecret">
            <abbr class="slds-required" title="required">* </abbr>Consumer Secret
          </label>
          <div class="slds-form-element__control">
            <input id="consumerSecret" class="slds-input" type="text" v-model="appSetting.ConsumerSecret"/>
          </div>
        </div>
        <div class="slds-form-element form-section last-form-section">
          <span class="slds-form-element__label">Callback URL</span>
          <!-- <div class="slds-form-element__control">
            <span class="slds-form-element__static static-url-value">{{ appSetting.CallbackUrl }}</span>
            <copy-button
              :message="appSetting.CallbackUrl"
              target-name="link"
            >
            </copy-button>
          </div> -->
          <div class="slds-form-element__control">
            <input
              class="slds-input"
              :value="appSetting.CallbackUrl"
              type="text"
              readonly="readonly"
            />
            <copy-button
              class="copy-to-clipboard-button"
              :message="appSetting.CallbackUrl"
              targetName="Url"
            >
              <icon
                name="copy_to_clipboard"
                icon-class-name="slds-button__icon"
              >
              </icon>
            </copy-button>
          </div>
        </div>
      </section>
      <footer class="slds-modal__footer">
        <button class="slds-button slds-button_neutral" :disabled="!hasChanged || isSaving" @click="cancel()">Cancel</button>
        <button class="slds-button slds-button_brand" :disabled="!hasChanged || isSaving || !canSave" @click="saveAppSettings()">Save</button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { getMCConnectedAppSettings, saveMCConnectedAppSettings } from '../../src/api/charket-settings.js'

export default {
  name: 'ConnectedAppSettings',
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Connected App Settings'
      },
      appSetting: {},
      appSettingCache: {},
      isSaving: false
    }
  },
  computed: {
    hasChanged () {
      return this.appSetting.ConsumerKey !== this.appSettingCache.ConsumerKey ||
        this.appSetting.ConsumerSecret !== this.appSettingCache.ConsumerSecret
    },
    canSave () {
      return this.appSetting.ConsumerKey && this.appSetting.ConsumerSecret
    }
  },
  created () {
    this.fetchAppSettings()
  },
  methods: {
    async fetchAppSettings () {
      try {
        this.$bus.emit('show-loading')
        this.appSetting = await getMCConnectedAppSettings()
        this.appSettingCache = JSON.parse(JSON.stringify(this.appSetting))
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.$bus.emit('hide-loading')
      }
    },
    cancel () {
      this.appSetting = JSON.parse(JSON.stringify(this.appSettingCache))
    },
    async saveAppSettings () {
      try {
        this.isSaving = true
        let tempAppSetting = {
          ConsumerKey: this.appSetting.ConsumerKey,
          ConsumerSecret: this.appSetting.ConsumerSecret
        }
        let result = await saveMCConnectedAppSettings(JSON.stringify(tempAppSetting))
        this.appSetting.ConsumerKey = result.ConsumerKey
        this.appSetting.ConsumerSecret = result.ConsumerSecret
        this.appSettingCache = JSON.parse(JSON.stringify(this.appSetting))
        this.$bus.emit('success', { content: 'Connected App Settings was saved.', type: 'success' })
      } catch (error) {
        this.$bus.emit('error', { content: error.msg, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>

